import React, { Component } from "react";
import Mentor from "./SVGs/mentor.svg";

class Intro extends Component {
  render() {
    const { nextScreen, playerName } = this.props;
    return (
      <div className={"intro"}>
        <button className={"button-intro"} onClick={nextScreen}>
          Start
        </button>
        <p className={"intro-text"}>
          Hallo {playerName}, <br />
          stell dir vor dein groes Ziel ist es, eines Tages ein erfolgreicher
          Knstler zu werden, der sich mit dem Schaffen von Skulpturen und
          Installationen beschftigt. Diese Leidenschaft ist in deiner Kindheit
          entstanden und hat sich als einfaches Hobby zu deinem Traum
          entwickelt. Du mchtest anderen Menschen helfen, den Weg zur Kunst zu
          finden und sie genauso begeistern wie es Kunstwerke bei dir schaffen.
          <br /> <br />
          Momentan arbeitest du tagsber in einem Bro und fhlst dich dort
          nicht ausreichend kreativ ausgelastet. Nun mchtest du mit deiner
          ersten Skulptur beginnen, die du daraufhin verffentlichen kannst. Du
          wirst ab jetzt drei Tage deine Freizeit zwischen 18 Uhr abends und 6
          Uhr morgens erleben und erhltst die Chance deinen Traum zu erfllen,
          wenn du dich von anderen Faktoren nicht zu sehr ablenken lsst. Du
          solltest immer deine Gesundheit im Blick behalten und dementsprechend
          berlegte Entscheidungen treffen. Diese werden Konsequenzen fr deine
          Zukunft haben.
        </p>
        <img src={Mentor} className={"intro-mentor"} />
      </div>
    );
  }
}

export default Intro;
